@charset "utf-8";
@import "_smart-grid";



.section__bg{
	margin-top: 100px;
	position: relative;
	max-width: 1920px;
	width: 100%;
	margin: auto;
	height: 100%;
	background-image: url('../img/mask.png');
	background-position-y: 800px;
	background-repeat: no-repeat;
	background-size: cover;
	z-index: 1;

	@include xs(margin-top, -100px);
	@include xs(background-position-y, 1000px);
	@include xs(background-size, 100% 55%);


}
.services {
	//border: 1px solid black;
	margin-top: 196px;
	position: relative;
	width: 100%;
}

.services__box {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	//width: 100%;
	//border: 1px solid black;
}

.services__title {
	margin-bottom: 50px;

	h2 {
		font-family: 'Geometria';
		font-size: 34px;
		line-height: 51px;
		text-align: center;
		letter-spacing: 0.02em;
		color: #2EC4B6;
	}
}

.services__left-img {
	position: absolute;
	top: -70px;
	left: -92px;
	z-index: 1;
}

.services__right-img {
	position: absolute;
	top: -87px;
	right: -95px;
}

.services__bottom-img {
	position: absolute;
	bottom: -186px;
	left: -91px;

	@include xs(left, -115px);	
}

.services__block {
	margin: 0 2px;
	position: relative;
	width: 680px;
	height: 350px;
	cursor: pointer;

	//@include xs(margin, 0 9px);


	

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 580px;
		height: 350px;
		border-radius: 20px;
		background-image: url('../img/rectangle-1.png');
		background-repeat: no-repeat;
		background-size: cover;
		z-index: -3;
	}

	h3 {
		position: relative;
		padding-top: 166px;
		font-family: 'Geometria';
		font-size: 28px;
		line-height: 42px;
		text-align: center;
		letter-spacing: 0.02em;
		color: #ffffff;


		@include xs(padding-top, 80px);
		@include xs(font-size, 20px);
		@include xs(line-height, 30px);

		&::before {
			content: "";
			position: absolute;
			bottom: 10px;
			width: 200px;
			border-bottom: 7px solid #2EC4B6;
			z-index: -1;
		}
	}

	p {
		display: none;
	}

	button {
		display: none;
	}

	&:hover {
	

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			z-index: -3;
			opacity: .3;
		}

		h3 {
			//border: 1px solid black;
			transition-duration: .8s;
			position: relative;
			padding-top: 100px;
			font-family: 'Geometria';
			font-size: 28px;
			line-height: 42px;
			text-align: center;
			letter-spacing: 0.02em;
			color: #1A183D;

			@include xs(padding-top, 80px);
			@include xs(font-size, 20px);
			@include xs(line-height, 30px);

			&::before {
				content: "";
				position: absolute;
				bottom: 10px;
				width: 200px;
				border-bottom: 7px solid #2EC4B6;
				z-index: -1;
			}
		}

		p {
			margin-top: 2px;
			display: block;
			font-family: 'Geometria';
			font-weight: 300;
			font-style: normal;
			font-size: 20px;
			line-height: 35px;
			text-align: center;
			color: #1A183D;

			@include xs(font-size, 18px);
			@include xs(line-height, 30px);
		}

		button {
			display: block;
			width: 182px;
			height: 50px;
			background: #2EC4B6;
			border-radius: 3px;
			margin: auto;
			cursor: pointer;
			font-family: 'Geometria';
			font-size: 12px;
			line-height: 15px;
			text-align: center;
			letter-spacing: 0.05em;
			text-transform: uppercase;
			color: #FFFFFF;
			padding-top: 6px;
			border: none;

			&:focus {
				outline: none;
				//border: none;
			}

		}
	}
}

