@charset "utf-8";
@import "_smart-grid";

.footer {
	//border: 1px solid black;
	margin-top: 50px;
	width: 100%;
	display: flex;
	flex-direction: column;
}

.footer__top {
	display: flex;
	justify-content: space-between;
	align-items: center;

	.footer__top-logo {}

	.footer__top-menu {

		.footer__menu {
			font-family: 'Geometria';
			font-size: 12px;
			line-height: 15px;
			text-transform: uppercase;
			color: #1A183D;
		}

		.footer__menu-link {
			padding-right: 17px;
		}


		@include xs(display, none);
	}

	.footer__top-tel {

		.header__contact-number {
			position: relative;
			font-family: 'Geometria';
			font-size: 20px;
			line-height: 30px;
			text-align: right;
			color: #1A183D;


			&::after {
				content: "";
				position: absolute;
				left: -25px;
				top: 8px;
				right: 0;
				background-image: url('../img/tel.svg');
				background-repeat: no-repeat;
				height: 17px;
			}
		}
	}
}

.footer__center {
	//border: 1px solid black;
	position: relative;
	width: 100%;
	display: flex;
	justify-content: center;

	.footer__center-icon {


		img {
			margin-right: 20px;
		}
	}


	.footer__center-img {
		//border: 1px solid black;
		position: absolute;
		top: -73px;
		right: 20%;

		@include xs(top, -32px);
		@include xs(right, -2%);
	}
}


.footer__bottom {
	display: flex;
	justify-content: space-between;
	align-items: center;

	@include xs(flex-wrap, wrap);
	@include xs(margin-top, 15px);

	.footer__bottom-left {

		@include xs(order, 2);
		p {
			font-family: 'Geometria';
			font-weight: 200;
			font-size: 18px;
			line-height: 28px;
			color: #1A183D;
			opacity: 0.5;

			@include xs(font-size, 16px);

			@include xsl(font-size, 15px);
		}
	}

	.footer__bottom-right {

		@include xs(order, 1);
		@include xs(margin, auto);
	}
}


.footer__menu-burger{

	display: none;
	transform: rotate(180deg);

	@include xs(display, block);
}