@charset "utf-8";
@import "_smart-grid";
// @import "settings/_settings";
@import "fonts/_fonts";


.header {
	padding-top: 36px;
	max-width: 1920px;
	width: 100%;
	height: 1300px;
	background-image: url('../img/bg-header.png');
	background-repeat: no-repeat;
	background-size: 100% 100%;
	margin: auto;


	@include xs(background-size, 220% 100%);
}

.pos {
	margin-left: 11px;

	@include xs(margin-left, 0px);
	@include xs(width, 100%);
}

.one {
	margin-left: 335px;

	@include xs(margin-left, 0px);
	@include xs(width, 100%);
}

.two {
	margin-left: 17px;
	///border: 1px solid rgb(3, 41, 253);

	@include xs(margin-left, 0px);
	@include xs(width, 100%);

}

.three {
	margin-left: 17px;

	@include xs(margin-left, 0px);
	@include xs(width, 100%);
}


.header__contact {
	//border: 1px solid black;
	margin-left: 64px;

	@include xs(display, none);

	.header__contact-number {
		font-family: 'GeometriaBold';
		font-size: 16px;
		line-height: 24px;
		align-items: center;
		text-align: right;
		color: #1A183D;
		position: relative;

		&::after {
			content: "";
			position: absolute;
			left: -20px;
			top: 2px;
			right: 0;
			background-image: url('../img/tel.svg');
			background-repeat: no-repeat;
			height: 17px;
		}
	}
}


.header__box {
	//border: 1px solid black;
	width: 100%;
	display: flex;
	justify-content: space-between;
}

.header__menu-burger {
	align-items: center;


	img {
		width: 100%;

		@include xs(width, 30px);
	}
}

.header__menu-block {
	display: flex;
	align-items: center;

	


	.header__menu {
		//border: 1px solid black;
		display: flex;
		align-items: center;

		@include xs(display, none);
		@include xs(position, absolute);
		@include xs(z-index, 1);
		@include xs(top, 110px);
		@include xs(right, 0);
		@include xs(width, 100%);
		@include xs(background, #2EC4B6);
		@include xs(padding-top, 40px);
		//@include xs(padding-bottom, 20px);
	}
}

.header__menu-link {
	font-family: 'GeometriaBold';
	font-size: 12px;
	line-height: 15px;
	text-transform: uppercase;
	color: #1A183D;

	@include xs(font-size, 25px);
	@include xs(margin-bottom, 30px);
	@include xs(text-align, center);
	@include xs(color, #ffff);
	@include xs(transition, .3s);

	//transition-duration: .8s;
	&:hover{
		@include xs(font-size, 30px);
	}
}

.header__button {
	width: 184px;
	height: 55px;
	background: #2EC4B6;
	border-radius: 3px;
	box-sizing: border-box;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	font-family: 'GeometriaMedium';
	font-size: 12px;
	line-height: 15px;
	letter-spacing: 0.05em;
	text-transform: uppercase;
	color: #FFFFFF;
	cursor: pointer;

	&:focus {
		outline: none;
		border: none;
	}

}

.header__center {
	//border: 1px solid black;
	position: relative;
	margin-top: 17px;
	background-image: url('../img/bg.png');
	background-repeat: no-repeat;
	background-size: 100% auto;
	background-position: center;
	height: 694px;
	width: 100%;


	@include xs(background-size, 140% 53%);
	@include xs(height, 500px);

	.img__oil {
		position: absolute;
		bottom: -85px;
		left: -93px;


		@include xs(left, -60px);
		@include xs(bottom, 58px);
		@include xs(width, 34%);

	}

	.img__per {
		position: absolute;
		right: 28%;
		bottom: -110px;

		@include xs(right, -50px);
		@include xs(bottom, 55px);
		@include xs(width, 34%);
	}
}

.header__logo-top {
	//border: 1px solid black;
	width: 100%;
	display: flex;
	justify-content: center;

}

.header__logo {
	//border: 1px solid black;
	margin-top: 20px;
	width: 100%;
	display: flex;
	justify-content: center;

	@include xs(margin-top, -95px);

	img {

		//width: 100%;
		@include xs(width, 100px);
	}

}


.header__title {
	width: 100%;
	margin-top: 20px;

	@include xs(margin-top, 45px);

	@include xsl(margin-top, 30px);

	h1 {
		font-family: 'GeometriaBold';
		font-style: normal;
		font-weight: bold;
		font-size: 48px;
		line-height: 54px;
		text-align: center;
		letter-spacing: 0.02em;
		text-transform: uppercase;
		color: #1A183D;

		@include xs(font-size, 20px);
		@include xs(line-height, 37px);
	}
}

.header__subtitle {
	margin-top: 5px;

	h3 {
		font-family: 'GeometriaMedium';
		font-weight: 500;
		font-size: 20px;
		line-height: 30px;
		text-align: center;
		color: #1A183D;
	}
}

.header__vek {
	//border: 1px solid black;
	margin-top: 36px;
	width: 100%;
	display: flex;
	justify-content: center;
	animation: move 2s ease-out infinite;


	@include xs(margin-top, -12px);

	img {
		height: 94px;

		svg {
			fill: green;
		}
	}

}

@keyframes move {
	25% {
		opacity: 1;
	}

	33% {
		opacity: 1;
		transform: translateY(10px);
	}

	67% {
		opacity: 0.7;
		transform: translateY(20px);
	}

	100% {
		opacity: 0;
		transform: translateY(35px) scale3d(0.5, 0.5, 0.5);
	}
}

.obout {
	//border: 1px solid black;
	position: relative;
	margin-top: 96px;
	width: 100%;

	.obout__left-img {
		position: absolute;
		top: 7px;
		left: -100px;

		@include xs(left, -50px);
		@include xs(top, -80px);

		img{

			@include xs(width, 150px);
		}
	}

	.obout__right-img {
		//border: 1px solid black;
		position: absolute;
		top: -50px;
		right: -92px;

		@include xs(right, -20px);
		@include xs(top, -85px);

		@include xsl(right, -35px);
		img{

			@include xs(width, 130px);
		}
	}

	.obout__lime-img {
		position: absolute;
		bottom: -80px;
		left: 390px;
		z-index: -2;

		@include xs(bottom, -100px);
		@include xs(left, 162px);
		@include xs(z-index, 0);
	
	}
}

.obout__tittle {

	h2 {
		font-family: 'GeometriaBold';
		font-size: 34px;
		line-height: 51px;
		text-align: center;
		letter-spacing: 0.02em;
		color: #2EC4B6;
	}
}

.obout__subtittle {
	//border: 1px solid black;
	width: 776px;
	margin: auto;


	@include xs(width, 100%);

	p {
		font-family: 'Geometria';
		font-weight: 500;
		font-size: 20px;
		line-height: 30px;
		text-align: center;
		color: #1A183D;

		@include xs(font-size, 15px);
	

		span {
			position: relative;
			z-index: 1;

			&::before {
				content: "";
				position: absolute;
				border-bottom: 3px solid #2EC4B6;
				bottom: 2px;
				left: 0;
				width: 100%;
				z-index: -1;
			}
		}
	}
}


.obout__box {
	//border: 1px solid rgb(219, 16, 192);
	margin-top: 50px;
	display: flex;
	justify-content: space-between;

	@include xs(overflow-y, scroll);

}

.obout__block {
	//border: 1px solid rgb(219, 16, 192);
	width: 210px;
	height: 281px;
	background: #FFFFFF;
	box-shadow: 0px 0px 24px rgba(26, 24, 61, 0.1);
	border-radius: 20px;
	cursor: pointer;

	@include xs(box-shadow, 7px 1px 6px rgba(26, 24, 61, 0.1));
	@include xs(margin-right, 15px);
	//@include xs(margin-right, 15px);

	.obout__block-img {
		padding-top: 24px;
		padding-left: 20px;

		svg {
			fill: #2EC4B6;
		}
	}

	.obout__block-title {
		padding-top: 19px;
		padding-left: 20px;

		h3 {
			font-family: 'Geometria';
			font-size: 16px;
			line-height: 24px;
			color: #1A183D;

			@include xs(width, 250px);
		}
	}

	.obout__block-subtitle {
		padding-left: 20px;
		padding-right: 20px;

		p {
			font-family: 'Geometria';
			font-style: normal;
			font-weight: 300;
			font-size: 14px;
			line-height: 22px;
			color: #1A183D;
		}
	}

	&:hover {
		background: #2EC4B6;

		.obout__block-img {

			svg {
				fill: #FFFFFF;
			}
		}

		.obout__block-title {

			h3 {
				color: #ffffff;
			}
		}

		.obout__block-subtitle {

			p {
				color: #ffffff;
			}
		}
	}
}