@charset "utf-8";
@import "_smart-grid";

.org {
	position: relative;
	width: 100%;
	margin-top: 155px;

	.org__left-top {
		position: absolute;
		left: 0;
		top: -242px;
		z-index: -1;

		@include xs(top, -100px);	

		img{
			@include xs(width, 215px);	
		}
	}

	.org__left-bottom {
		position: absolute;
		bottom: -58px;
		left: -94px;

		@include xs(bottom, -100px);	
		@include xs(left, -100px);	

		img{
			@include xs(width, 215px);	
		}
	}

	.org__right-top {
		position: absolute;
		top: -100px;
		right: -100px;


		@include xs(top, -43px);	
		@include xs(right, -20px);	

		img{

			@include xs(width, 115px);	
		}

	}

	.org__right-bottom {
		position: absolute;
		right: -90px;
		bottom: 0;
		z-index: -2;
	}
}

.org__box {
	height: 396px;
	background: #2EC4B6;
	border-radius: 10px;

	@include xs(height, 720px);


	h2 {
		padding-top: 48px;
		font-family: 'Geometria';
		font-size: 28px;
		line-height: 42px;
		text-align: center;
		letter-spacing: 0.02em;
		color: #FFFFFF;
	}

	h4 {
		font-family: 'Geometria';
		font-size: 20px;
		line-height: 30px;
		text-align: center;
		color: #FFFFFF;
		max-width: 803px;
		width: 100%;
		margin: auto;
		padding-top: 10px;

		@include xs(font-size, 17px);
		@include xs(max-width, 325px);	

		@include xsl(max-width, 299px);	
	}
}

.org__form {
//	border: 1px solid black;
	padding-top: 20px;
	margin: auto;
	max-width: 495px;
	width: 100%;
	display: flex;
	margin: auto;
	justify-content: center;
	//flex-wrap: wrap;

	@include xs(flex-direction, column);
	@include xs(align-content, center);
	@include xs(margin-top, 20px);

	.input__name {

		
		position: relative;
		font-family: 'Geometria';
		font-size: 16px;
		line-height: 20px;
		text-align: center;
		color: #ffffff;
		background: none;
		border: none;

		@include xs(display, flex);
		@include xs(margin,auto);

		

		&::placeholder {
			opacity: .6;
			color: rgb(255, 255, 255);
			border-bottom: 4px solid rgb(255, 255, 255);
		}

		&:focus {
			outline: none;
		}

	}

	.input__tel {
		margin-left: 15px;
		// max-width: 200px;
		// width: 100%;
		position: relative;
		font-family: 'Geometria';
		font-size: 16px;
		line-height: 20px;
		text-align: center;
		color: #ffffff;
		background: none;
		border: none;

		@include xs(display, flex);
		@include xs(margin,auto);
		@include xs(margin-top, 20px);

		&::placeholder {
			opacity: .6;
			color: rgb(255, 255, 255);
			border-bottom: 4px solid rgb(255, 255, 255);
		}

		&:focus {
			outline: none;
		}
	}

	.input__submit {
		//border: 1px solid black;
		margin-top: 35px;
		margin-left: 34%;

		width: 125px;
		height: 55px;
		background: #FFFFFF;
		border-radius: 3px;
		font-family: 'Geometria';
		font-size: 12px;
		line-height: 15px;
		text-align: center;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: #1A183D;
		cursor: pointer;

		&:focus {
			outline: none;
			border: none;
		}
	}
}